.login {
	background: #18191e;
	width: 504px;
	height: 450px;
	box-shadow: 0 3px 40px rgb(0 30 69 / 16%);
	border-radius: 10px;
	padding: 48px 32px;
}

.icon {
	aspect-ratio: 106 / 75;
	width: 70px;
	margin: 0 auto;
	margin-bottom: 24px;
	background: center / cover no-repeat;
	background-image: url(data:image/svg+xml;base64,);

}

.title {
	font-weight: 500;
	font-size: 20px;
	line-height: 28px;
	text-align: center;
	margin-bottom: 48px;
}